/** Media Queries **/
/*utilizar en H1, H2, H3*/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Outfit", sans-serif;
  font-size: 1.8rem;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

h1, h2, h3 {
  font-family: "Outfit", sans-serif;
  color: #1a1b15;
  margin: 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.text-center {
  text-align: center;
}

/*
 *SELECTOR DE ATRIBUTOS
 * $ elementos cuyo atributo termina con...
 * ^ elementos cuyo atributo comienza con...
 * * elementos cuyo atributo contenga...
 * ~ elementos cuyo atributo class contenga...
*/
[class$=__heading] {
  font-family: "Outfit", sans-serif;
  color: #1a1b15;
  margin: 2rem 0;
  text-align: center;
  margin: 6rem 0 3rem 0;
}

[class$=__descripcion] {
  text-align: center;
  font-weight: 300;
  font-size: 1.8rem;
  margin-bottom: 5rem;
  color: #64748B;
}

