.paquete {
  border: 1px solid #64748B;
  padding: 3rem;
}
.paquete__nombre {
  font-weight: 900;
  font-size: 2.4rem;
  margin: 2rem 0;
}
.paquete:nth-child(2) {
  order: -1;
}
@media (min-width: 768px) {
  .paquete:nth-child(2) {
    order: unset;
  }
}
.paquete:nth-child(3) {
  order: -1;
}
@media (min-width: 768px) {
  .paquete:nth-child(3) {
    order: unset;
  }
}
@media (min-width: 768px) {
  .paquete {
    order: unset;
  }
}
.paquete__lista {
  margin: 0;
  padding: 0;
  list-style: none;
  line-height: 1.5;
}
.paquete__precio {
  font-weight: 700;
  font-size: 6rem;
  color: #007df4;
  text-align: center;
}

.agenda {
  /*1200 px*/
  width: min(95%, 140rem);
  margin: 0 auto;
  padding: 5rem 0;
}

.eventos__heading {
  color: #007df4;
  margin: 5rem 0;
  font-weight: 700;
  font-size: clamp(2rem, 8vw, 6rem);
  text-align: left;
}
.eventos__fecha {
  color: #64748B;
}
.eventos--workshops .eventos__heading {
  color: #00c8c2;
}
.eventos--workshops .evento__informacion {
  background-color: #00c8c2;
}
.eventos--workshops .evento__informacion:hover {
