.ponente {
  border: 1px solid #64748B;
  padding: 3rem;
  background-position: center top;
  background-repeat: no-repeat;
  background-size: 35rem;
}
.ponente__nombre {
  font-weight: 900;
  font-size: 2.4rem;
  margin: 2rem 0;
}
.ponente:nth-child(4n+1) {
  background-image: image-set(url("../img/bg_1.avif") 1x, url("../img/bg_1.webp") 1x, url("../img/bg_1.png") 1x);
}
.ponente:nth-child(4n+2) {
  background-image: image-set(url("../img/bg_2.avif") 1x, url("../img/bg_2.webp") 1x, url("../img/bg_2.png") 1x);
}
.ponente:nth-child(4n+3) {
  background-image: image-set(url("../img/bg_3.avif") 1x, url("../img/bg_3.webp") 1x, url("../img/bg_3.png") 1x);
}
.ponente:nth-child(4n+4) {
  background-image: image-set(url("../img/bg_4.avif") 1x, url("../img/bg_4.webp") 1x, url("../img/bg_4.png") 1x);
}
.ponente__ubicacion {
  color: #64748B;
  line-height: 1.5;
}
.ponente__listado-skills {
  padding: 0;
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}
.ponente__skill {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  gap: 0.7rem;
  align-items: center;
  justify-content: center;
  background-color: #007df4;
  border-radius: 0.5rem;
  padding: 1rem;
  width: auto;
}
.ponente__skill > li {
  color: #FFFFFF;
  font-size: 1.6rem;
}
.ponente__skill > i {
  color: #FFFFFF;
  font-size: 1.8rem;
}
.ponente__skill > i:hover {
  cursor: pointer;
  color: #ff4343;
}

.ponente-sociales {
  display: flex;
  gap: 2rem;
}
.ponente-sociales__enlace::before {
  color: #1E293B;
  font-size: 2rem;
  display: inline-block;
  font-style: normal;
  text-rendering: auto;
  -webkit-font-smooting: antialiased;
  font-family: "Font Awesome 6 Brands";
}
.ponente-sociales__enlace[href*="facebook.com"]::before {
  content: "\f39e";
}
.ponente-sociales__enlace[href*="github.com"]::before {
  content: "\f09b";
}
.ponente-sociales__enlace[href*="tiktok.com"]::before {
  content: "\e07b";
}
.ponente-sociales__enlace[href*="instagram.com"]::before {
  content: "\f16d";
}
.ponente-sociales__enlace[href*="youtube.com"]::before {
  content: "\f167";
}
.ponente-sociales__enlace[href*="twitter.com"]::before {
  content: "\f099";
}
.ponente-sociales__ocultar {
  display: none;
}

