}

.boleto {
  width: min(100% - 5rem, 65rem);
  margin: 0 auto;
  padding: 6rem;
  border-radius: 2rem;
  color: #FFFFFF;
  position: relative;
}
.boleto::before, .boleto::after {
  position: absolute;
  content: "";
  width: 5rem;
  height: 5rem;
  background-color: #FFFFFF;
  top: calc(50% - 2.5rem);
}
.boleto::before {
  clip-path: circle(50% at 15% 50%);
  left: 0%;
}
.boleto::after {
  clip-path: circle(50% at 85% 50%);
  right: 0%;
}
.boleto--presencial {
  background-color: hsl(178, 100%, 39%);
  background-image: radial-gradient(at 50% 20%, hsl(209, 100%, 47%) 0px, transparent 50%), radial-gradient(at 80% 13%, hsl(178, 100%, 39%) 0px, transparent 50%), radial-gradient(at 87% 65%, hsl(209, 100%, 47%) 0px, transparent 50%), radial-gradient(at 14% 12%, hsl(178, 100%, 39%) 0px, transparent 50%), radial-gradient(at 0% 100%, hsl(209, 100%, 47%) 0px, transparent 50%), radial-gradient(at 48% 57%, hsl(178, 100%, 39%) 0px, transparent 50%), radial-gradient(at 0% 0%, hsl(209, 100%, 47%) 0px, transparent 50%);
}
.boleto--virtual {
  background-color: #00c8c2;
}
.boleto--gratis {
  background-color: #007df4;
}
.boleto__logo {
  font-size: clamp(2rem, -0.8rem + 10vw, 4rem);
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .boleto__logo {
    margin: 0;
    text-align: left;
  }
}
.boleto__plan {
  font-size: 2rem;
  text-transform: uppercase;
  margin: 3rem 0 0 0;
}
.boleto__precio {
  font-size: 4rem;
  font-weight: 700;
  margin: 3rem 0 0 0;
}

